import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import Button from "@material-ui/core/Button";

import Item from "../ListItem/ListItem";
import Result from "../Result/Result";

import "./Container.css";

const availableElementsInitalState = [
  { id: 1, shortName: "BG", name: "Bruno Gonçalves", checked: true },
  { id: 2, shortName: "FN", name: "Filipa Nunes", checked: true },
  { id: 3, shortName: "JV", name: "Joel Viegas", checked: true },
  { id: 4, shortName: "JS", name: "João Santos", checked: true },
  { id: 5, shortName: "MF", name: "Marcos Freitas", checked: true },
  { id: 6, shortName: "HB", name: "Hugo Brito", checked: true },
  { id: 7, shortName: "LL", name: "Luis Lopes", checked: true },
  { id: 8, shortName: "FS", name: "Flávio Silva", checked: true },
];

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    maxWidth: 360,
    backgroundColor: "#c4161d",
  },
  button: {
    height: "2rem",
  },
}));

const Container = () => {
  const classes = useStyles();

  const [isLoading, setIsLoading] = useState(false);
  const [lucky, setLucky] = useState(null);
  const [availableElements, setAvailableElements] = useState(
    availableElementsInitalState
  );

  const handleChange = (id) => (e) => {
    const aggResult = availableElements.reduce((agg, element) => {
      if (element.id === id) {
        element.checked = !element.checked;
      }

      agg.push(element);

      return agg;
    }, []);

    console.log(aggResult);

    setAvailableElements(aggResult);
  };

  const getRandom = (max) => Math.floor(Math.random() * (max - 0) + 0);

  const handleStartRandom = () => {
    setLucky(null);

    const availableCheckedElements = availableElements.filter(
      (element) => element.checked
    );

    if (availableCheckedElements.length === 0) {
      alert("Please selected persons");
      return;
    }

    setIsLoading(true);

    setTimeout(() => {
      setIsLoading(false);

      setLucky(
        availableCheckedElements[getRandom(availableCheckedElements.length)]
      );
    }, 5000);
  };

  return (
    <div id="videobackground" className="container">
      <div className="flex1Center">
        <img
          alt="critical logo"
          style={{ width: "25rem", height: "25rem" }}
          src={require(`../../images/critical.png`)}
        />
      </div>
      <div className="contentContainer">
        <div className="title">Working random generator</div>
        <div className="list">
          <List dense className={classes.root}>
            {availableElements.map((teamElement) => {
              return <Item teamElement={teamElement} onChange={handleChange} />;
            })}
          </List>
        </div>
        <Button
          disabled={isLoading}
          variant="contained"
          color="primary"
          onClick={handleStartRandom}
        >
          Start
        </Button>
      </div>
      <div className="contentContainer">
        <div className="title" />
        <Result teamElement={lucky} isLoading={isLoading} />
      </div>
    </div>
  );
};

export default Container;
