import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Countdown from "react-countdown";

import "./Result.css";

const Result = ({ teamElement, isLoading }) => {
  const useStyles = makeStyles({
    root: {
      display: "flex",
      justifyContent: "center",
      alignSelf: "center",
      maxWidth: 345,
      backgroundColor: "#c4161d",
      height: teamElement ? "24rem" : "20rem",
      width: "20rem",
      border: "2px solid white",
    },
    rootArea: {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
    },
  });

  const classes = useStyles();

  return (
    <Card className={classes.root}>
      <CardActionArea className={{ root: { ...classes.rootArea } }}>
        {isLoading ? (
          <div className="countdownContainer">
            <Countdown
              date={Date.now() + 5000}
              renderer={({ seconds }) => {
                return (
                  <div className="timerContainer">
                    <span className="timerValue">{seconds}</span>
                  </div>
                );
              }}
            />
          </div>
        ) : (
          <CardMedia
            component="img"
            alt={teamElement ? teamElement.shortName : "default"}
            style={{
              height: "20rem",
              width: "20rem",
            }}
            image={
              teamElement
                ? require(`../../images/${teamElement.shortName}.png`)
                : require(`../../images/placeholder.png`)
            }
            title={teamElement ? teamElement.shortName : ""}
          />
        )}
        <CardContent>
          <span className="selectedName">
            {teamElement ? teamElement.name : ""}
          </span>
        </CardContent>
      </CardActionArea>
    </Card>
  );
};

export default Result;
